#app {
    size: 100vw 100vh;
    position: relative;

    .logo {
        position: relative;

        span {
            font: 600 44px/120% var(--font1);
            letter-spacing: 2px;
            text-transform: uppercase;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-image: linear-gradient(90deg, #0065ff, #6942ef, #6554c0, #008cff, #0065ff, #6942ef);
            background-size: 500%;
            background-position: 0% 0%;
        }
    }

    .sub-logo {
        letter-spacing: 1.5px;
        text-transform: uppercase;
        font: 300 10px/100% var(--font1);
    }

    .search {
        top: 40px;
        left: 40px;
        position: absolute;
        width: 500px;
        z-index: 10;

        button {
            position: absolute;
            left: 4px;
            top: 50%;
            transform: translateY(-50%);
            size: 36px;
            // border-radius: 10px;
            // box-shadow: 0 0 20px var(--color3);

            svg {
                color: #fff;
                size: 20px;
            }
        }

        input {
            // height: 64px;
            padding: 14px 15px 14px 50px;
            width: inherit;
            background-color: var(--color3);
            border: 0;
            // border-radius: 10px;
            // box-shadow: 0 0 20px var(--color3);

            &::placeholder {
                font: 400 12px/150% var(--font1);
                color: var(--color2);
                text-transform: uppercase;
            }
        }
    }

    >.left {
        width: 70vw;
        height: 100vh;

        .bg {
            width: 70vw;
            height: 100vh;

            ul {
                size: 100%;
                min-height: inherit !important;

                li {
                    size: inherit;

                    .author {
                        background: var(--color1);
                        position: absolute;
                        right: 40px;
                        bottom: 40px;
                        z-index: 10;
                        height: 48px;

                        .avatar {
                            size: 48px;
                        }

                        .info {
                            padding: 10px 20px;
                            color: var(--color2);

                            .title {
                                font: 600 12px/150% var(--font1);
                            }

                            .name {
                                font: 400 10px/150% var(--font1);
                                letter-spacing: .2px;
                            }
                        }
                    }

                    img {
                        size: inherit;
                        object-fit: cover;
                        object-position: center;
                    }
                }
            }
        }
    }

    >.right {
        width: 30vw;
        height: 100vh;

    }


}

.tabs {
    width: 100%;

    .tab {
        width: 100%;
    }
}